    startReconcile() {
      console.log('starting recon!');
      this.SAPData.headers.push({"text":"Matches","value":"matches"});

      const SAPDataValues = this.SAPData.items.map(item => item.amount);
      const depositSlipValues = this.depositSlipData.items.map(item => item.amount);
      const logFilesDataValues = this.logFilesData.items.map(item => item.amount);

      this.SAPData.items.forEach(item => {
        if ( depositSlipValues.includes(item.amount) ) {
          item.matches = 'Deposit: ';
          item.matches += this.depositSlipData.items.filter(depositSlipItem => depositSlipItem.amount === item.amount)[0].name;
        }
        if ( logFilesDataValues.includes(item.amount) ) {
          item.matches = 'Log: ';
          item.matches += this.logFilesData.items.filter(logItem => logItem.amount === item.amount)[0].name;
        }
      });

      this.depositSlipData.headers.push({"text":"Matches","value":"matches"});
      this.depositSlipData.items.forEach(item => {
        if ( SAPDataValues.includes(item.amount) ) {
          item.matches = 'SAP';
        }
      });

      this.logFilesData.headers.push({"text":"Matches","value":"matches"});
      this.logFilesData.items.forEach(item => {
        if ( SAPDataValues.includes(item.amount) ) {
          item.matches = 'SAP';
        }
      });
      
      this.reconResultsText = "results";
    },
    handleSAPDataTableClick(event) {
      console.log('event', event);
    },
    handleSAPFileSelect(file) {
      console.log('handleSAPFileSelect called', file);
      if ( file ) {
        this.parseExcel(file, (worksheetData) => {
          const amountKey = '__EMPTY_6';
          const referenceKey = '__EMPTY_7';
          const cleanedSAPList = [];
          this.SAPData = {};
          this.SAPData.total = 0;
          worksheetData.forEach(row => {
            if ( row[amountKey] && row[referenceKey] ) {
              const amount = parseFloat(row[amountKey].replace(/[$,]/g,''));
              if (amount) {
                cleanedSAPList.push({ amount, reference: row[referenceKey] });
                this.SAPData.total += parseFloat(amount);
              }
            }
          });
          this.SAPData.headers = [{ text: 'Reference', value: 'reference' }, { text: 'Amount', value: 'amount' }];
          this.SAPData.items = cleanedSAPList;
          console.log(JSON.stringify(this.SAPData));
        });
      }
    },
    handleDepositSlipInput() {
      const input = this.depositSlipDataTextInput
      this.depositSlipDataTextInput = null;
      // console.log('handleDepositSlipInput', input);
      const inputList = input.split('\n');
      if (! this.depositSlipData ) {
        this.depositSlipData = {};
        this.depositSlipData.items = [];
        this.depositSlipData.total = 0;
      }
      const cleanedDepositSlipList = [];
      this.depositSlipCount++;
      for (let i = 0; i < inputList.length; i++) {
        // Assuming the delimiters are integers between 0 - 999;
        const isValidDelimiter = inputList[i].length <= 3 && ! inputList[i].includes('.') && parseInt(inputList[i]) > 0;
        if ( isValidDelimiter ) {
          const amount = parseFloat(inputList[i+2].replace(/[$,]/g,''));
          const reference = inputList[i+1];
          cleanedDepositSlipList.push({ amount, reference, name: `Slip ${this.depositSlipCount}` });
          this.depositSlipData.total += parseFloat(amount);
          i+=2;
        }
      }
      this.depositSlipData.headers = [{ text: 'Name', value: 'name' }, { text: 'Reference', value: 'reference' }, { text: 'Amount', value: 'amount' }];
      this.depositSlipData.items = [...this.depositSlipData.items, ...cleanedDepositSlipList];
      console.log(JSON.stringify(this.depositSlipData));
    },
    handleLogFileSelect(files) {
      console.log('handleLogFileSelect called', files);
      this.numFilesToProcess = files.length;
      this.numFilesProcessed = 0;

      this.logFilesData = {};
      this.logFilesData.headers = [
        { text: 'Name', value: 'name' },
        { text: 'Date', value: 'date' },
        { text: 'Reference', value: 'reference' },
        { text: 'Amount', value: 'amount' }
      ];
      this.logFilesData.items = [];
      this.logFilesData.total = 0;

      files.forEach(file => {
        this.logFilesDataInProgress = true;
        if ( file ) {
          console.log('file', file);
          const name = file.name.split(' ')[0];
          this.parseExcel(file, (worksheetData) => {
            this.numFilesProcessed += 1;
            // console.table(worksheetData);
            const amountKey = '__EMPTY_3';
            const referenceKey = '__EMPTY_2';
            const dateKey = '__EMPTY_1';
            const cleanedLogFileList = [];
            worksheetData.forEach(row => {
              if (row[amountKey] && row[referenceKey] && row[dateKey]) {
                if ( row[dateKey] != 'Deposit Date' ) {
                  const date = new Date(row[dateKey]).toISOString().substring(0, 10);
                  if ( date === this.selectedDateFormatted ) {
                    const amount = parseFloat(row[amountKey].replace(/[$,]/g,''));
                    if ( amount ) {
                      console.log('pushing amount:', amount);
                      cleanedLogFileList.push({ 
                        name,
                        date,
                        amount: amount,
                        reference: row[referenceKey]
                      });
                      this.logFilesData.total += (amount);
                    }
                  }
                }
              }
            });
            this.logFilesData.items = [...this.logFilesData.items, ...cleanedLogFileList];
            if ( this.numFilesProcessed === this.numFilesToProcess ) {
              this.logFilesDataInProgress = false;
              console.log(JSON.stringify(this.logFilesData));
            }
          });
          }
      });
    },
    parseExcel(file, callback) {
      const reader = new FileReader();
      reader.readAsBinaryString(file);
      reader.onerror = function(error) {
        console.log('Error:', error);
      };
      reader.onload = function(e) {
        const data = e.target.result;
        const workbook = XLSX.read(data, {
          type: 'binary'
        });
        workbook.SheetNames.forEach(function(sheetName) {
          const worksheetObject = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName], {raw: false});
          callback(worksheetObject);
        })
      };
    },
    handleCalculate(e) {
      if (e.keyCode === 13) {
        this.calculateText = eval(this.calculateText);
      }    
    },
    formatDollar(text) {
      return text.toLocaleString('en-US', {
        style: 'currency',
        currency: 'USD'
      });
    }
  }
};
</script>
